import { Descendant } from 'slate';
import { DomEditor } from '../editor/dom-editor';
import { IDomEditor } from '../editor/interface';
import parseElemHtml from '../parse-html/parse-elem-html';
import $, { DOMElement, getTagName } from '../utils/dom';

export const blockElements = [
  'address',
  'article',
  'aside',
  'blockquote',
  'details',
  'dialog',
  'dd',
  'div',
  'dl',
  'dt',
  'fieldset',
  'figcaption',
  'figure',
  'footer',
  'form',
  'h1',
  'h2',
  'h3',
  'h4',
  'h5',
  'h6',
  'header',
  'hr',
  'li',
  'main',
  'nav',
  'ol',
  'p',
  'pre',
  'section',
  'table',
  'ul',
];

function isRepeatedCreate(
  editor: IDomEditor,
  attrKey: string,
  selector: string | DOMElement,
): boolean {
  // @ts-ignore
  const $elem = $(selector);
  if ($elem.attr(attrKey)) {
    return true; // 有属性，说明已经创建过
  }

  // 至此，说明未创建过，则记录
  $elem.attr(attrKey, 'true');

  // 销毁时删除属性
  editor.on('destroyed', () => {
    $elem.removeAttr(attrKey);
  });

  return false;
}

/**
 * 检查是否重复创建 textarea
 */
export function isRepeatedCreateTextarea(
  editor: IDomEditor,
  selector: string | DOMElement,
): boolean {
  return isRepeatedCreate(editor, 'data-k-e-textarea', selector);
}

/**
 * 检查是否重复创建 toolbar
 */
export function isRepeatedCreateToolbar(
  editor: IDomEditor,
  selector: string | DOMElement,
): boolean {
  return isRepeatedCreate(editor, 'data-k-e-toolbar', selector);
}

/**
 * 生成默认 content
 */
export function genDefaultContent(editor: IDomEditor) {
  const p = DomEditor.genEmptyParagraph(editor);
  return [p];
}

/**
 * 默认p标签
 */
export function getDefaultP(children: string, editor) {
  const {
    MENU_CONF: {
      lineHeight: { lineHeight },
      fontSize: { fontSize },
      color: { color },
    },
  } = editor.getConfig();
  return `<p style="margin: 0 0 8px 0;font-size: ${
    fontSize || '16px'
  };font-weight: normal;line-height: ${
    lineHeight || 1.75
  };color: ${color}">${children}</p>`;
}

export function removeEmptyElement(htmlDoc: Document) {
  const paragraphs = htmlDoc.querySelectorAll('p');
  const DIVS = htmlDoc.querySelectorAll('div');
  paragraphs.forEach((paragraph) => {
    const spanElement = paragraph.querySelector('span');
    const childNodes = Array.from(paragraph.childNodes);
    if (childNodes.length === 0) {
      paragraph.remove();
      return;
    }
    if (
      spanElement?.childElementCount === 0 &&
      spanElement?.textContent?.trim() === '' &&
      childNodes.length === 1 &&
      childNodes[0].nodeName.toLowerCase() === 'span'
    ) {
      paragraph.remove();
    }
  });
  DIVS.forEach((div) => {
    const spanElement = div.querySelector('span');
    const childNodes = Array.from(div.childNodes);
    if (childNodes.length === 0) {
      div.remove();
      return;
    }
    if (
      spanElement?.childElementCount === 0 &&
      spanElement?.textContent?.trim() === '' &&
      childNodes.length === 1 &&
      childNodes[0].nodeName.toLowerCase() === 'span'
    ) {
      div.remove();
    }
  });
  return htmlDoc;
}
export function removeImgBRImgElement(htmlDoc: Document): Document {
  const imgNodes = htmlDoc.querySelectorAll('img');
  const spanNodes = htmlDoc.querySelectorAll('span');

  //去除掉span与img标签之间的换行标签
  spanNodes.forEach((spanNode) => {
    const nextSibling = spanNode.nextSibling;
    if (
      nextSibling &&
      nextSibling.nodeName === 'BR' &&
      nextSibling.nextSibling &&
      nextSibling.nextSibling.nodeName === 'IMG'
    ) {
      nextSibling.remove();
    }
  });
  // 去除掉img与img标签之间的换行标签
  imgNodes.forEach((imgNode) => {
    const nextElementSiblingNodeName =
      imgNode.nextElementSibling?.nodeName || '';
    const previousElementSiblingNodeName =
      imgNode.previousElementSibling?.nodeName || '';
    if (
      nextElementSiblingNodeName === 'BR' &&
      previousElementSiblingNodeName === 'IMG'
    ) {
      imgNode.nextElementSibling?.remove();
    }
    if (
      blockElements.includes(imgNode.parentNode?.nodeName.toLowerCase() || '')
    ) {
      if (
        blockElements.includes(nextElementSiblingNodeName.toLowerCase()) ||
        blockElements.includes(previousElementSiblingNodeName.toLowerCase())
      ) {
        if ($(imgNode).attr('src') === '') return imgNode.remove();
        const p = document.createElement('p');
        p.appendChild(imgNode.cloneNode(true));
        imgNode?.parentNode?.replaceChild(p, imgNode);
      }
    }
  });
  return htmlDoc;
}
export function filterSpanElement(htmlDoc: Document): Document {
  const spanElements = htmlDoc.querySelectorAll('span');
  spanElements.forEach((span) => {
    const emElements = span.querySelectorAll('em');
    emElements.forEach((em) => {
      const childNodes = Array.from(em.childNodes);
      if (childNodes.length === 0) {
        em.textContent = ' ';
      }
    });
  });
  spanElements.forEach((spanNode) => {
    const nextElementSiblingNodeName =
      spanNode.nextElementSibling?.nodeName || '';
    const previousElementSiblingNodeName =
      spanNode.previousElementSibling?.nodeName || '';
    if (
      blockElements.includes(spanNode.parentNode?.nodeName.toLowerCase() || '')
    ) {
      if (
        blockElements.includes(nextElementSiblingNodeName.toLowerCase()) ||
        blockElements.includes(previousElementSiblingNodeName.toLowerCase())
      ) {
        if (spanNode.childNodes.length === 0) return spanNode.remove();
        const p = document.createElement('p');
        p.appendChild(spanNode.cloneNode(true));
        spanNode?.parentNode?.replaceChild(p, spanNode);
      }
    }
  });
  return htmlDoc;
}
/**
 * 生成p标签下包裹的span标签
 */
export function handleKleinEditorHtml(htmlString: string) {
  const parser = new DOMParser();
  let htmlDoc = parser.parseFromString(htmlString, 'text/html');
  htmlDoc = removeEmptyElement(htmlDoc);
  htmlDoc = removeImgBRImgElement(htmlDoc);
  htmlDoc = filterSpanElement(htmlDoc);

  const bodyHtml = htmlDoc.body.innerHTML.replace(
    /(style="[^"]*(?:\s*!\s*important)?[^"]*")/g,
    (match, group) => {
      return group.replace(/\s*!\s*important/g, '');
    },
  );
  console.log(bodyHtml, 'htmlDoc.body.innerHTML');
  return bodyHtml;
}
/**
 * html 字符串 -> content
 * @param editor editor
 * @param html html 字符串
 */
export function htmlToContent(
  editor: IDomEditor,
  html: string = '',
): Descendant[] {
  // 空白内容
  if (html === '') html = getDefaultP('<br>', editor);

  // 非 HTML 格式，文本格式，用 <p> 包裹
  if (html.indexOf('<') !== 0) {
    html = html
      .split(/\n/)
      .map((line) => getDefaultP(line, editor))
      .join('');
  }
  const $html = handleKleinEditorHtml(`<div>${html}</div>`);
  const $content = $($html);
  let arr: any = [];
  Array.from($content.children()).map((child) => {
    const $child = $(child);
    arr.push(parseElemHtml($child, editor));
  });
  return arr;
}
