/**
 * @description SelectList class
 *
 */

import $, { Dom7Array } from '../../utils/dom';
import { IOption, ISelectMenu } from '../interface';
import PanelAndModal from './BaseClass';
import { IDomEditor } from '../../editor/interface';
import { SVG_CHECK_MARK } from '../../constants/svg';

// “对号”icon
function gen$SelectedIcon() {
  return $(SVG_CHECK_MARK);
}

class SelectList extends PanelAndModal {
  type = 'selectList';
  readonly $elem: Dom7Array = $(`<div class="k-e-select-list"></div>`);

  constructor(editor: IDomEditor, width?: number, menu?: ISelectMenu) {
    super(editor);

    if (width) {
      this.$elem.css('width', `${width}px`);
    }
    if (menu?.wide) {
      this.$elem.addClass('k-e-select-list-wide');
    }

    this.$elem.on('click', (e: Event) => {
      // selectList 如有滚动条，可能会点击拖拽，参考 https://github.com/wangeditor-team/wangEditor-v5/issues/325
      // 此时需要阻止冒泡，因为在 $container.on('mousedown', () => editor.hidePanelOrModal()) ，$container 就是 `.k-e-text-container`
      e.stopPropagation();
    });
  }

  /**
   * 渲染 list
   * @param options select options
   */
  renderList(options: IOption[]) {
    const $elem = this.$elem;
    $elem.empty(); // 先清空内容，再重新渲染

    const $list = $(`<ul></ul>`);

    options.forEach((opt) => {
      const {
        value,
        text,
        selected,
        styleForRenderMenuList,
        line,
        title,
        textIsTag,
        showSelectedIcon = true,
      } = opt;
      /**
       * 自定义需求：当value及text均为null或-，则不需要创建item（场景：font-size未匹配，显示空）。
       */
      if (!!value || !!text) {
        if (!(text === '-' || value === '-')) {
          const $li = $(`<li data-value="${value}"></li>`); // 【注意】必须用 <li> 必须用 data-value！！！

          if (styleForRenderMenuList) {
            $li.css(styleForRenderMenuList);
          }

          if (line) {
            $li.addClass('k-e-select-list-line');
          }

          if (selected) {
            if (showSelectedIcon) {
              const $selectedIcon = gen$SelectedIcon();
              $selectedIcon.addClass('k-e-select-list-selected');
              $li.append($selectedIcon);
            }
            $li.addClass('selected');
          }

          if (textIsTag) {
            const $text = $(text).attr('data-value', value);
            $li.append($text);
          } else {
            $li.append($(`<span data-value="${value}">${text}</span>`));
          }

          $li.attr(
            'title',
            title !== null && typeof title !== 'undefined' ? title : text,
          );
          $list.append($li);
        }
      }
    });
    $elem.append($list);
  }

  genSelfElem(): Dom7Array | null {
    return null;
  }
}

export default SelectList;
