import { IDropPanelMenu } from '@klein-editor/editor';
/**
 * @description dropPanel class
 *
 */

import { IDomEditor } from '../../editor/interface';
import $, { Dom7Array } from '../../utils/dom';
import PanelAndModal from './BaseClass';

class DropPanel extends PanelAndModal {
  type = 'dropPanel';
  readonly $elem: Dom7Array = $(`<div class="k-e-drop-panel"></div>`);

  constructor(editor: IDomEditor, menu?: IDropPanelMenu) {
    super(editor);

    if (menu?.dropPanelMinWidth) {
      this.$elem.css('min-width', `${menu?.dropPanelMinWidth}px`);
    }

    if (menu?.dropPanelClassName) {
      this.$elem.addClass(menu?.dropPanelClassName);
    }
  }

  genSelfElem(): Dom7Array | null {
    return null;
  }
}

export default DropPanel;
