/**
 * @description 处理 drop 事件
 *
 */

import { IDomEditor } from '../../editor/interface';
import TextArea from '../TextArea';

function handleOnMouseUp(e: Event, textarea: TextArea, editor: IDomEditor) {
  if (editor.formatBrushStatus === 1) {
    editor.formatBrush(0, null);
  }
}

export default handleOnMouseUp;
