/**
 * @description parse-html helper fns
 *
 */
import { Descendant, Text } from 'slate';

const REPLACE_SPACE_160_REG = new RegExp(String.fromCharCode(160), 'g');

/**
 * 把 charCode 160 的空格（`&nbsp` 转换的），替换为 charCode 32 的空格（JS 默认的）
 * @param str str
 * @returns str
 */
export function replaceSpace160(str: string): string {
  const res = str.replace(REPLACE_SPACE_160_REG, ' ');
  return res;
}

/**
 * 往 children 最后一个 item（如果是 text node） 插入文字
 * @param children children
 * @param str str
 * @returns 是否插入成功
 */
export function tryInsertTextToChildrenLastItem(
  children: Descendant[],
  str: string,
): boolean {
  const len = children.length;
  if (len) {
    const lastItem = children[len - 1];
    if (Text.isText(lastItem)) {
      const keys = Object.keys(lastItem);
      if (keys.length === 1 && keys[0] === 'text') {
        // lastItem 必须是纯文本，没有 marks
        lastItem.text = lastItem.text + str;
        return true;
      }
    }
  }
  return false;
}

export function checkChildNodeChildren(childNodeChildren): boolean {
  if (Array.isArray(childNodeChildren)) {
    for (const child of childNodeChildren) {
      if (checkChildNodeChildren(child)) {
        return true;
      }
    }
  } else if (typeof childNodeChildren === 'object') {
    const hasType =
      childNodeChildren.hasOwnProperty('type') && childNodeChildren.type !== '';
    const hasText =
      childNodeChildren.hasOwnProperty('text') &&
      (childNodeChildren.text !== '' || childNodeChildren.text !== '\n');
    const style = childNodeChildren.style || {};
    const hasWidthAndHeight =
      style.hasOwnProperty('width') && style.hasOwnProperty('height');

    if (hasType || hasText || hasWidthAndHeight) {
      return true;
    }

    if (Array.isArray(childNodeChildren.children)) {
      if (checkChildNodeChildren(childNodeChildren.children)) {
        return true;
      }
    }
  }
  return false;
}

export function removeParentStructure(structure) {
  const parsedStructure = structure;
  const nodesToRemove: any = [];

  function findElementsWithIsEmptyText(node, parent) {
    if (node.isEmptyText === false) {
      nodesToRemove.push({ element: node, parent: parent });
    } else if (node.children) {
      for (const child of node.children) {
        findElementsWithIsEmptyText(child, node);
      }
    }
  }

  function hasSiblingWithEmptyText(parent, element) {
    if (parent && parent.children) {
      for (const child of parent.children) {
        if (child !== element && child.isEmptyText !== false) {
          return true;
        }
      }
    }
    return false;
  }

  function removeParent(node, parent) {
    if (parent && parent.children) {
      const index = parent.children.indexOf(node);
      if (index !== -1) {
        parent.children.splice(index, 1);
      }
      for (const child of parent.children) {
        removeParent(node, child);
      }
    }
  }

  for (const item of parsedStructure) {
    findElementsWithIsEmptyText(item, null);
    for (const {
      element: targetElement,
      parent: parentElement,
    } of nodesToRemove) {
      const allSiblingsEmptyText = hasSiblingWithEmptyText(
        parentElement,
        targetElement,
      );
      if (!allSiblingsEmptyText) {
        removeParent(parentElement, item);
      }
    }
  }

  return parsedStructure;
}
