/**
 * @description base button class
 *
 */
import { Editor } from 'slate';
import { IButtonMenu, IDropPanelMenu, IModalMenu } from '../interface';
import $, { Dom7Array } from '../../utils/dom';
import { IBarItem, getEditorInstance } from './index';
import { clearSvgStyle } from '../helpers/helpers';
import { promiseResolveThen } from '../../utils/util';
import { addTooltip } from './tooltip';

abstract class BaseButton implements IBarItem {
  readonly $elem: Dom7Array = $(`<div class="k-e-bar-item"></div>`);
  protected readonly $button: Dom7Array = $(`<button type="button"></button>`);
  menu: IButtonMenu | IDropPanelMenu | IModalMenu;
  private disabled = false;
  customHidePanelOrModal?: boolean;
  inGroup: boolean;
  $svg: Dom7Array | null;

  constructor(
    menu: IButtonMenu | IDropPanelMenu | IModalMenu,
    inGroup = false,
  ) {
    this.menu = menu;
    this.inGroup = inGroup;
    this.$svg = null;
    // 验证 tag
    const {
      title,
      hotkey = '',
      iconSvg = '',
      tag,
      width,
      alwaysShowTitle = false,
    } = menu;

    if (tag !== 'button')
      throw new Error(`Invalid tag '${tag}', expected 'button'`);

    // ----------------- 初始化 dom -----------------
    const { $button } = this;

    if (iconSvg) {
      this.$svg = $(iconSvg);
      clearSvgStyle(this.$svg); // 清理 svg 样式（扩展的菜单，svg 是不可控的，所以要清理一下）
      $button.append(this.$svg);
      if (alwaysShowTitle) {
        const $title = $(`<span class="k-e-bar-item-text">${title}</span>`);
        $button.append($title);
      }
    } else {
      // 无 icon 则显示 title
      $button.text(title);
    }
    addTooltip($button, iconSvg, title, hotkey, inGroup); // 设置 tooltip
    if (inGroup && iconSvg) {
      // in groupButton（且有 icon），显示 menu title
      // 如果没有 icon ，上面已添加 title ，不用重复添加
      $button.append($(`<span class="title">${title}</span>`));
    }
    if (width) {
      $button.css('width', `${width}px`);
    }
    this.$elem.append($button);

    // ----------------- 异步绑定事件 -----------------
    promiseResolveThen(() => this.init());
  }

  private init() {
    // 设置 button 属性
    this.setActive();
    this.setDisabled();
    this.setColor();

    // button click
    if (this.$svg?.is('.k-e-svg-font-colors-fill')) {
      this.onBarItemSplitClick('.k-e-svg-font-colors-fill', 'color');
      this.onBarItemClick('.k-e-bar-item-dropdown');
    } else if (this.$svg?.is('.k-e-svg-high-light-fill')) {
      this.onBarItemSplitClick('.k-e-svg-high-light-fill', 'backgroundColor');
      this.onBarItemClick('.k-e-bar-item-dropdown');
    } else {
      this.onBarItemClick();
    }
  }
  private onBarItemSplitClick(target = '', attr) {
    this.$button.on('click', target, (e) => {
      e.preventDefault();
      const editor = getEditorInstance(this);
      if (!this.customHidePanelOrModal) editor.hidePanelOrModal(); // 隐藏当前的各种 panel
      if (this.disabled) return;
      const curMarks = Editor.marks(editor);
      const markStyle = Object.assign({}, curMarks?.['style'] || {}, {
        [attr]: $(target).css('color'),
      });
      Editor.addMark(editor, 'style', markStyle);
    });
  }
  private onBarItemClick(target = '') {
    this.$button.on('click', target, (e) => {
      e.preventDefault();
      const editor = getEditorInstance(this);

      if (!this.customHidePanelOrModal) editor.hidePanelOrModal(); // 隐藏当前的各种 panel
      if (this.disabled) return;

      this.exec(); // 执行 menu.exec
      this.onButtonClick(); // 执行其他的逻辑
    });
  }
  /**
   * 执行 menu.exec
   */
  private exec() {
    const editor = getEditorInstance(this);
    const menu = this.menu;
    const value = menu.getValue(editor);
    menu.exec(editor, value);
  }

  // 交给子类去扩展
  abstract onButtonClick(): void;

  private setActive() {
    const editor = getEditorInstance(this);
    const { $button } = this;
    const active = this.menu.isActive(editor);
    const { title, hotkey = '', iconSvg = '', activeTitle = '' } = this.menu;
    const className = 'active';
    if (active) {
      // 设置为 active
      if (activeTitle)
        addTooltip($button, iconSvg, activeTitle, hotkey, this.inGroup); // 设置 tooltip
      $button.addClass(className);
    } else {
      // 取消 active
      if (activeTitle)
        addTooltip($button, iconSvg, title, hotkey, this.inGroup); // 设置 tooltip
      $button.removeClass(className);
    }
  }

  private setColor() {
    // 针对背景色和颜色两个功能做特殊处理
    const editor = getEditorInstance(this);
    const { containerId } = editor.getConfig();
    const curMarks = Editor.marks(editor);
    if (curMarks) {
      // const style = curMarks['style'] || {};
      const colorVal = $(`#${containerId} .k-e-svg-font-colors-fill`).css(
        'color',
      );
      const bgColorVal = $(`#${containerId} .k-e-svg-high-light-fill`).css(
        'color',
      );

      const selectors = [
        {
          s: `#${containerId} .k-e-svg-font-colors-fill`,
          key: colorVal,
        },
        {
          s: `#${containerId} .k-e-svg-high-light-fill`,
          key: bgColorVal,
        },
      ];
      selectors.forEach((selector) => {
        if (selector.key) {
          $(selector.s).css('color', selector.key);
          $(selector.s)
            .children()
            .each((ele, i) => {
              if (i == 1) {
                $(ele).attr({
                  fill: 'currentColor',
                });
              }
            });
        }
      });
    }
  }

  private setDisabled() {
    /**
     * 自定义需求：除编辑器readOnly状态及指定menuKey，否则工具栏不禁用
     */
    const editor = getEditorInstance(this);
    const sourceEditor = editor.getConfig().sourceEditor;
    const { $button } = this;
    const className = 'disabled';
    let disabled = false;
    if (editor.isDisabled() || sourceEditor) {
      disabled = true;
    }

    // 指定menu key触发
    const key = this.menu.key || '';
    if (!sourceEditor && ['undo', 'redo'].indexOf(key) > -1) {
      disabled = this.menu.isDisabled(editor);
    }
    if (sourceEditor && ['codeBlock'].indexOf(key) > -1) {
      disabled = this.menu.isDisabled(editor);
    }

    if (disabled) {
      disabled = true;
      // 设置为 disabled
      $button.addClass(className);
    } else {
      disabled = false;
      // 取消 disabled
      $button.removeClass(className);
    }
    this.disabled = disabled;

    /**
     * 初始禁用逻辑
     */
    // const editor = getEditorInstance(this);
    // const { $button } = this;
    // let disabled = this.menu.isDisabled(editor);

    // if (editor.selection == null || editor.isDisabled()) {
    //   // 未选中，或者 readOnly ，强行设置为 disabled
    //   disabled = true;
    // }

    // // 永远 enable
    // if (this.menu.alwaysEnable) disabled = false;

    // const className = 'disabled';
    // if (disabled) {
    //   // 设置为 disabled
    //   $button.addClass(className);
    // } else {
    //   // 取消 disabled
    //   $button.removeClass(className);
    // }

    // this.disabled = disabled; // 记录下来
  }

  changeMenuState() {
    this.setActive();
    this.setDisabled();
    this.setColor();
  }
}

export default BaseButton;
